body {
  margin: 0;
}

#app {
  display: flex;
  height: 100vh;
}

.main-content {
  width: 75%;
  padding: 20px;
}

.sidebar {
  width: 25%;
  background-color: aliceblue;
  padding: 20px;
}

.toolbar {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 20px;
  background-color: whitesmoke;
  padding: 10px;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: none;
  align-items: center;
  justify-content: center;

  &.active {
    display: flex;
  }
}

#list-recipes {
  height: calc(100vh - 60px);
  overflow: auto;
}

#search-recipes {
  margin-bottom: 10px;
  input {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
  }
}

#create-recipe-form {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
}
